<template>
  <div class="bottom-style">
    <div class="bottom-left">
<!--      <div class="bottom-left-main border-style1">-->
<!--        <div class="title">客户满意率</div>-->
<!--        <div-->
<!--          class="charts-style"-->
<!--          v-loading="!customerSatisfactionRes"-->
<!--          element-loading-background="rgba(0, 0, 0, 0.2)"-->
<!--          element-loading-text="加载中"-->
<!--        >-->
<!--          <bda-line-charts-->
<!--            v-if="customerSatisfactionRes"-->
<!--            :chartData="customerSatisfactionLineData"-->
<!--            :yDw="dw"-->
<!--            :showLegend="false"-->
<!--          ></bda-line-charts>-->
<!--        </div>-->
<!--      </div>-->
      <div class="bottom-left-main border-style2">
        <div class="title">业务类型客户满意率</div>
        <div
          class="charts-style"
          v-loading="!customerSatisfactionForTypeRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <bda-bar-charts
            v-if="customerSatisfactionForTypeRes"
            :chartsConfig="customerSatisfactionForTypeBarData"
            :barWidth="4"
            :legendStatus="false"
            :yAxiosDw="dw"
            :percentRange="[60, 100]"
          ></bda-bar-charts>
        </div>
      </div>
    </div>
    <div class="bottom-right">
      <div class="bottom-right-main1 border-style3">
        <div class="title">主城渝西客户满意率</div>
        <div
          class="charts-style"
          v-loading="!customerSatisfactionForFilialeRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <bda-bar-charts
            v-if="customerSatisfactionForFilialeRes"
            :chartsConfig="customerSatisfactionForFilialeBarData"
            :barWidth="6"
            :legendStatus="false"
            :yAxiosDw="dw"
            :xVertical="true"
            :percentRange="[80, 100]"
          ></bda-bar-charts>
        </div>
      </div>
      <div class="bottom-right-main2 border-style4">
        <div class="title">渝东南北客户满意率</div>
        <div
          class="charts-style"
          v-loading="!customerSatisfactionForRes"
          element-loading-background="rgba(0, 0, 0, 0.2)"
          element-loading-text="加载中"
        >
          <bda-bar-charts
            v-if="customerSatisfactionForRes"
            :chartsConfig="customerSatisfactionForBarData"
            :barWidth="6"
            :legendStatus="false"
            :yAxiosDw="dw"
            :percentRange="[80, 100]"
            :xVertical="true"
          ></bda-bar-charts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '../../../base/mixin/service-index-control';
// import bdaLineCharts from '../../../widgets/bda-line-charts/bda-line-charts';
import qs from 'qs';
import bdaBarCharts from '../../../widgets/bda-3d-barCharts/bda-3d-barCharts';

export default {
  name: "businessProcessBottom",
  mixins: [Interface],
  components: {
    // bdaLineCharts,
    bdaBarCharts },
  data() {
    return {
      customerSatisfactionLineData: {},    //客户满意率
      customerSatisfactionRes: false,
      dw: '%',
      customerSatisfactionForTypeRes: false,   //业务类型客户满意率
      customerSatisfactionForTypeBarData: {},
      customerSatisfactionForFilialeRes: false,    //供电分公司客户满意率
      customerSatisfactionForFilialeBarData: {},
      customerSatisfactionForRes: false,   //供电有限责任公司满意率
      customerSatisfactionForBarData: {}
    }
  },
  watch: {},
  created() {
  },
  mounted() {
    this.getCustomerSatisfaction(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //   window.console.log('客户满意率', res);
      this.customerSatisfactionData(res);
    }).catch(() => {
      this.customerSatisfactionRes = true;
    });
    this.getCustomerSatisfactionForType(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //    window.console.log('业务类型客户满意率', res);
      this.CustomerSatisfactionDataFilter(res, '1')
    }).catch(() => {
      this.customerSatisfactionForTypeRes = true;
    });
    this.getCustomerSatisfactionForFiliale(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //   window.console.log('供电分公司客户满意率', res)
      this.CustomerSatisfactionDataFilter(res, '2')
    }).catch(() => {
      this.customerSatisfactionForFilialeRes = true;
    });
    this.getCustomerSatisfactionFor(qs.stringify({
      dimensionality: this.$parent.dateType,
      timeStr: this.$parent.dateParams
    })).then(res => {
      //  window.console.log('供电有限责任公司满意率', res);
      this.CustomerSatisfactionDataFilter(res, '3')
    }).catch(() => {
      this.customerSatisfactionForRes = true;
    });
  },
  methods: {
    customerSatisfactionData(data) {
      let xData = [], dataInfo = [];
      if (data) {
        for (let item of data) {
          xData.push(item.dates);
          dataInfo.push(item.per);
        }
      }
      this.customerSatisfactionRes = false;
      this.customerSatisfactionLineData = {
        dimension: xData,
        dataMap: { '客户满意度': dataInfo },
        color: ['#2ed89a']
      };
      this.$nextTick(() => {
        this.customerSatisfactionRes = true;
      })
    },
    //业务客户类型满意率
    CustomerSatisfactionDataFilter(data, type) {
      let xData = [], dataInfo = [];
      if (data) {
        for (let item of data) {
          dataInfo.push((item.per !== (undefined || null)) ? item.per : 0);
          if (type === '1') {
            xData.push(item.busisubtype);
          } else {
            // xData.push(item.orgname ? item.orgname.replace('江津区', '江津') : '');
            xData.push(item.orgname);

          }
        }
      }
      if (type === '1') {
        this.customerSatisfactionForTypeRes = false;
        this.customerSatisfactionForTypeBarData = {
          dimension: xData,
          quotaMap: { '业务类型客户满意率': dataInfo },
          colorMap: { '业务类型客户满意率': ['#00E2FF', '#0074FF'] }
        };
        this.$nextTick(() => {
          this.customerSatisfactionForTypeRes = true;
        })
      } else if (type === '2') {
        this.customerSatisfactionForFilialeRes = false;
        this.customerSatisfactionForFilialeBarData = {
          dimension: xData,
          quotaMap: { '供电分公司客户满意率': dataInfo },
          colorMap: { '供电分公司客户满意率': ['#00FFEF', '#007EFF'] }
        };
        this.$nextTick(() => {
          this.customerSatisfactionForFilialeRes = true;
        })
      } else {
        this.customerSatisfactionForRes = false;
        this.customerSatisfactionForBarData = {
          dimension: xData,
          quotaMap: { '供电有限责任公司满意率': dataInfo },
          colorMap: { '供电有限责任公司满意率': ['#6AFFA2', '#00BDFA'] }
        };
        this.$nextTick(() => {
          this.customerSatisfactionForRes = true;
        })
      }

    },

  }
}
</script>

<style scoped lang="scss">
.bottom-style {
  width: 100%;
  height: 100%;
  position: relative;
  .title {
    color: #00e7ff;
    width: 100%;
    height: 23%;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .charts-style {
    height: 77%;
    width: calc(100% - 40px);
    padding: 0 20px;
    position: relative;
  }
  .border-style1 {
    background: url("../../../assets/img/管理框4-1.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .border-style2 {
    background: url("../../../assets/img/管理框4-2.png") no-repeat top center;
    background-size: 104% 100%;
  }
  .border-style3 {
    background: url("../../../assets/img/管理框5.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .border-style4 {
    background: url("../../../assets/img/管理框6.png") no-repeat top center;
    background-size: 100% 100%;
  }
  .bottom-left {
    width: 36%;
    float: left;
    height: 100%;
    position: relative;
    display: flex;
    .bottom-left-main {
      flex: 1;
      height: 100%;
      float: left;
      position: relative;
    }
  }

  .bottom-right {
    width: 64%;
    float: left;
    height: 100%;
    display: flex;
    .bottom-right-main1,
    .bottom-right-main2 {
      display: flex;
      float: left;
      height: 100%;
      flex-direction: column;
    }
    .bottom-right-main1 {
      width: 45%;
    }
    .bottom-right-main2 {
      width: 55%;
    }
  }
}
</style>